const STORAGE_KEY = "yoTalkConversations";

export const saveConversations = (conversations) => {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(conversations));
}; // JSON 

export const loadConversations = () => {
  const data = localStorage.getItem(STORAGE_KEY); //  localStoragekey data
  if (!data) return {};
  const parsed = JSON.parse(data); //dataJSON.parseparsed
  const fixed = {}; //

  for (const [id, value] of Object.entries(parsed)) {
    //
    if (Array.isArray(value)) {
      // valuemessages createdAt
      fixed[id] = {
        messages: value,
        createdAt: extractTimestampFromId(id),
      };
    } else {
      // messages+createdAt
      fixed[id] = {
        ...value, //value
        createdAt: value.createdAt || extractTimestampFromId(id),
      };
    }
  }

  return fixed; //
};

const extractTimestampFromId = (id) => {
  const match = id.match(/^(\d{4})\.(\d{2})\.(\d{2})/); //  YYYY.MM.DD
  if (match) {
    const [, year, month, day] = match;
    return new Date(`${year}-${month}-${day}`).getTime();
  }
  return Date.now();
}; //id
